import "./App.css";
import { useEffect, useRef, useState } from "react";
import Axios from 'axios';

//const accessToken = "4993dc22134c116d35c28dea1f969926b8e66447";

function App3() {
	const [longUrl, setLongUrl] = useState("");
	const [errMess, setErrMess] = useState("");
	const [shortenedUrl, setShortenedUrl] = useState("");
	const shortenedinput = useRef(null);

	const outputShortLinkElement = document.querySelector("#output-short-link");

    const outputDivElement = document.querySelector(".output");
	const api = "https://crud-url-shortner.herokuapp.com/url/";
	const submitre = () =>{
	   Axios.post("https://crud-url-shortner.herokuapp.com/api/create-short-url", {
		 longUrl:longUrl,
		 shortenedUrl:shortenedUrl
	 })
	  };
   

	useEffect(() => {
		if (errMess) {
			const timeout = setTimeout(() => {
				setErrMess("");
			}, 3000);
			return () => {
				clearTimeout(timeout);
			};
		}
	}, [errMess]);

	function handleCopy() {
		if (shortenedUrl) {
			navigator.clipboard.writeText(shortenedUrl);
		}
		submitre();
	}

	function handleSubmit(e) {
		e.preventDefault();
		if (longUrl) {
			const url = {
				long_url: longUrl,
			};
			shortenUrl(url);
		} else {
			setErrMess("Please Enter a link");
		}
	}

	const shortenUrl= function shortenUrl(url) {
		setShortenedUrl("");
		fetch(api, {
			method: "POST",
			headers: {
			  Accept: "application/json",
			  "Content-Type": "application/json",
			},
			body: JSON.stringify({
			  longUrl: longUrl,
			}),
		  })
			.then((res) => {
		
			  if (res.status === 200) {
				return res.json();
			  } else {
				throw new Error(`Api not responding`);
			  }
			})
			.then((data) => {
			  outputShortLinkElement.innerText = data.shortUrl;
			  outputShortLinkElement.href = data.shortUrl;
			  outputDivElement.style.display = "block";
			  setShortenedUrl(data);
			  console.log(shortenUrl);
			  //outputDivElement.style.visibility = "visible";
			})
			.catch((error) => {
			  console.error("Api not responding!!");
			 // errorDivElement.style.display = "block";
			  //errorDivElement.style.visibility = "visible";
			});
/*	(function () {
        fetch(host + "api/get-all-short-urls")
          .then(function (response) {
            return response.json();
          })
          .then(function (data) {
            let html = "";
            for (let i = 0; i < data.length; i++) {
              html += (
                <tr>
                  <td>${data[i].longurl}</td>
                  <td>
                    ${host}${data[i].shorturlid}
                  </td>
                  <td>${data[i].count}</td>
                </tr>
              );
            }
            document.querySelector("#list_urls tbody").innerHTML = html;
          })
          .catch(function (error) {
            alert("Something went wrong");
          });
      })();*/
	}
	return (
		<main className="mainn">
		<header className="header">
			<h2>Create Short URL</h2>
		</header>
		<section className="App">
            <div className="form">
			<h2 className="labell">Paste your long URL here:</h2>
			<p className="error-message">{errMess}</p>
			<div className="input">
				<form onSubmit={handleSubmit}>
					<div className="form-control">
						<input
							type="text"
							placeholder="long url"
							value={longUrl}
							onChange={(e) => setLongUrl(e.target.value)}
						/>
						<button type="submit" id="create-short-url"  >Shorten</button>
					</div>
				</form>
			</div>
			<div className="output" id="output-div" >
				<p  className="labell" >Short URL:</p>
				<div className="form-control" id="output-short-link">
					<input
						type="text"
						name="short-url"
						
						value={shortenedUrl}
						ref={shortenedinput}
						onChange={(e)=>{setShortenedUrl(e.target.value)}}
						
						readOnly
					/>
					</div>
					<button type="button" onClick={handleCopy}>
						Copy
					</button>
				</div>
	
			</div>
	
		</section>
	</main>
				
	);
}

export default App3;