import React from "react";
import './App.css';
import history from './history';
import { useState } from "react"  ;
import Axios from 'axios';


function App(){
  const [user_name,setUser_name]=useState('');
 const [name,setName]=useState('');
 const [user_email,setUser_email]=useState('');
  const [password,setPassword]=useState('');


   const submitreg = () =>{
     alert("successful!");
    Axios.post("https://crud-url-shortner.herokuapp.com/api/insert", {
      user_name: user_name,
      name:name,
      password: password, 
      user_email:user_email
  }).then((res)=>{
  alert("successful");
  });
 history.push('/');
   };


  return( 
  <div className="regie">
  <div className="App"><h1>REGISTER </h1>
  <div className="form1">
  <label className="labell" >USERNAME:</label>
  <input type="text" name="user_name" onChange={(e)=>{setUser_name(e.target.value)}}/>
  <label className="labell">NAME:</label>
  <input type="text" name="name" onChange={(e)=> {setName(e.target.value)}}/>
  <label className="labell">EMAIL:</label>
  <input type="email" name="user_email" onChange={(e)=> {setUser_email(e.target.value)}}/>
  <label className="labell">PASSWORD:</label>
  <input type="text" name="password" onChange={(e)=> {setPassword(e.target.value)}}/>
  <button target="_blank" onClick={submitreg}>SUBMIT</button>
  </div>
  </div>
  </div>
  );
}
export default App ;
